import React from "react"
import styled from "styled-components"

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faEnvelope } from "@fortawesome/free-solid-svg-icons"
import { faGithub, faLinkedin } from "@fortawesome/free-brands-svg-icons"

import Layout from "../components/Layout"
import SEO from "../components/SEO"
import Intro from "../sections/Intro"
import About from "../sections/About"
import Experience from "../sections/Experience"
import Projects from "../sections/Projects"
import Contact from "../sections/Contact"

const Socials = styled.address`
  margin: 0;
  padding: 4rem 0;
  background-color: #1a202c;
  font-style: normal;
`

const SocialList = styled.ul`
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 auto;
  padding: 0 1rem;
  max-width: 960px;
  list-style-type: none;
`

const Social = styled.li`
  margin: 0.1rem 0;
`

const SocialLink = styled.a.attrs({
  target: "_blank",
  rel: "noreferrer",
})`
  margin: 0 0.5rem;
  color: white;
  text-decoration: none;
`

const Footer = () => (
  <Socials>
    <SocialList>
      <Social>
        <SocialLink href="https://github.com/jason-crowley">
          <FontAwesomeIcon icon={faGithub} />
          jason-crowley
        </SocialLink>
      </Social>
      <Social>
        <SocialLink href="https://www.linkedin.com/in/jason-crowley">
          <FontAwesomeIcon icon={faLinkedin} />
          jason-crowley
        </SocialLink>
      </Social>
      <Social>
        <SocialLink href="mailto:jason_crowley@brown.edu">
          <FontAwesomeIcon icon={faEnvelope} />
          jason_crowley@brown.edu
        </SocialLink>
      </Social>
    </SocialList>
    {/* &copy; {new Date().getFullYear()}, Built with */}
    {/* {` `} */}
    {/* <a href="https://www.gatsbyjs.org">Gatsby</a> */}
  </Socials>
)

const IndexPage = () => (
  <Layout header={<Intro />} footer={<Footer />}>
    <SEO title="Home" />
    <About />
    <Experience />
    <Projects />
    {/* <Contact /> */}
  </Layout>
)

export default IndexPage
